___CSS_LOADER_EXPORT___.push([module.id, `/*--THE MAYER RESET--*/
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*End of the mayer reset*/

* {
  box-sizing: border-box;
}

@font-face {
  font-family: roboto;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
  font-family: robotBold;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

:root.light {
  --main-text-clr: #242423;
  --second-text-clr: #4d524d;
  --gold-clr: #f5cb5c;
  --main-background-clr: #e8eddf;
  --secondary-background-clr: #cfdbd5;
  --agree-clr: #50c878;
  --agree-clr-hover: #1e9f49;
  --danger-clr: #e03e39;
  --hot-danger-clr: #f92b24;
}

body {
  margin: 0;
  background-color: var(--main-background-clr);
  font-family: roboto, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

div.container,
.wrapper {
  height: 100vh;
}

.card-container {
  overflow-y: scroll;
}

.primary-clr {
  color: var(--main-text-clr);
}

.secondary-clr {
  color: var(--second-text-clr);
}

.wrapper.grid {
  display: grid;
  grid-template-columns: 20% 80%;
  grid-template-areas: "nav cardContainer";
}

.wrapper.block {
  display: block;
}

aside.left-side {
  padding: 1rem;
  background-color: var(--second-text-clr);
  color: var(--secondary-background-clr);
  height: 100%;
  grid-area: nav;
}

.detail {
  margin: 1rem;
  cursor: pointer;
  user-select: none;
}

.projects-header {
  text-align: center;
}

.card-container {
  grid-area: cardContainer;
}

.card-navigation {
  padding: 1rem;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1rem;
}

button {
  cursor: pointer;
}

.create-btn,
.create-project-btn{
  background-color: var(--agree-clr);
  border: none;
  color: white;
  padding: 0.6rem 1rem;
  display: inline-block;
}

.new-todo-btn{
  background-color: var(--agree-clr);
  color: white;
  width: 40px;
  height: 40px;
}

.create-project-btn {
  display: block;
  margin: 1rem auto;
}

.card-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-auto-rows: 250px;
  padding: 1rem;
  gap: 1rem;
}

.card {
  padding: 1rem;
  box-shadow: 2px 2px 50px rgba(0, 0, 0, 0.026);
  overflow-x: auto;
  height: 100%;
  border-radius: .3rem;
}

.card:hover{
  box-shadow: 2px 2px 50px rgba(0, 0, 0, 0.2);
  transition: .5s ease-in;
}

.card .title {
  color: var(--gold-clr);
  margin-bottom: 1rem;
  font-size: 2rem;
}

.current-prj-header {
  color: var(--main-text-clr);
  font-family: robotoBold, roboto, system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
  font-weight: bold;
}

.card .btns {
  display: flex;
  margin: auto;
  width: 50%;
  margin-top: 1rem;
  gap: 1rem;
}

.card .btns > * {
  flex: 1;
  padding: 0.4rem;
}

.remove-btn {
  background-color: var(--danger-clr);
  color: white;
  border: none;
}

.remove-btn:hover {
  background-color: var(--hot-danger-clr);
}

.create-btn:hover,
.new-todo-btn:hover,
.create-project-btn:hover {
  background-color: var(--agree-clr-hover);
}

.card .creation-date,
.card .due-date,
.card .priority {
  margin-top: 0.5rem;
  color: var(--second-text-clr);
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  border-radius: 1rem;
}

textarea {
  resize: none;
}

.modal-content {
  display: flex;
}

div.close {
  cursor: pointer;
  margin-left: auto;
}

.modal-content * {
  display: block;
  margin: 1rem;
  flex: 1;
}

.modal-content .left-side {
  margin-right: auto;
}

.modal-content .close {
  flex: 0 0 10px;
}

.modal-content {
  color: var(--second-text-clr);
}

.close {
  font-size: 2rem;
}

.project-btn {
  background-color: transparent;
  color: var(--main-background-clr);
  border: none;
  padding: 1rem 2rem;
  display: block;
}

textarea,
input {
  box-sizing: border-box;
  padding: 10px;
  border: 2px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  margin-bottom: 15px;
  transition: border-color 0.3s ease;
  width: 100%;
  border-left: 5px solid var(--agree-clr);
}

textarea:invalid,
input:invalid{
  border-left: 5px solid var(--danger-clr);
}

input[type="color"] {
  width: 100px;
  height: 50px;
  padding: 0;
  border: none;
  cursor: pointer;
}

/* Hover effect */
input:hover {
  border-color: #666;
}

/* Focus effect */
input:focus {
  border-color: #007bff;
  outline: none;
}

/* Placeholder color */
::placeholder {
  color: #aaa;
}

.invalid {
  margin: auto;
  color: var(--danger-clr);
}

button{
  border-radius: .2rem;
  border: none;
}

@media only screen and (max-width: 70rem) {
  .wrapper.grid {
    display: block;
  }
  div.container,
  .wrapper.grid {
    height: fit-content;
  }
  aside.left-side {
    border: none;
  }
  .modal-content {
    width: 100%;
    height: 100%;
  }
  div.modal-content,
  .modal-content form {
    padding: 0;
    margin: 0;  
    border-radius: 0;
  }
  textarea,
  input {
    width: 80vw;
  }
}
